import React, { FC, useState } from "react";
import styles from "./ConfirmModal.module.css";
import { Modal } from "@alfalab/core-components/modal";
import { CrossMIcon } from "@alfalab/icons-glyph/CrossMIcon";
import { ExclamationMIcon } from "@alfalab/icons-glyph/ExclamationMIcon";
import { ButtonDesktop } from "@alfalab/core-components/button/desktop";
import { useSelector } from "../../services/hooks";
import { isTemplate } from "../../services/selectors/taskSelector";

type TProps = {
  // readyToSave: boolean;
  formReady: boolean;
  isOpen: boolean;
  setTask: React.Dispatch<React.SetStateAction<boolean>>;
  setOpen: React.Dispatch<React.SetStateAction<boolean>>;
  setEdit: React.Dispatch<React.SetStateAction<boolean>>;
  handleCancel: () => void;
  handleSave: () => void;
  handleDelete: () => void;
  handleCreate: () => void;
};

const ConfirmModal: FC<TProps> = ({
  // readyToSave,
  formReady,
  isOpen,
  setTask,
  setOpen,
  setEdit,
  handleCancel,
  handleSave,
  handleCreate,
  handleDelete,
}) => {
  const template = useSelector(isTemplate);

  const handleCreateTask = () => {
    handleCreate();
    setOpen(false);
    setTask(false);
  };
  const handleSaveTask = () => {
    handleSave();
    setOpen(false);
    setTask(false);
  };

  const handleDeleteTask = () => {
    handleDelete();
    setOpen(false);
    setTask(false);
  };

  const handleCancelTask = () => {
    handleCancel();
    setOpen(false);
    setTask(false);
  };

  return (
    <Modal
      open={isOpen}
      disableBackdropClick={true}
      disableEscapeKeyDown={true}
      className={styles.modal}
      Backdrop={() => <div className={styles.modalBackdrop}></div>}
      zIndex={101}
    >
      <div id="modalConfirm" className={styles.modalContainer}>
        <CrossMIcon
          className={styles.modalAddon}
          onClick={() => {
            //      ,       
            // setTask(false);
            // setEdit(false);
            setOpen(false);
          }}
          fill="#BABBC2"
        />
        <h1 className={styles.modalHeaderText}></h1>
        <div className={styles.modalQuestion}>
          <div className={styles.backgroundIcon}>
            <ExclamationMIcon
              className={styles.exclamationIcon}
              fill="#F4963F"
            />
          </div>
          <p className={`text_type_small ${styles.modalMainText}`}>
            {template && formReady
              ? "     ?"
              : template
              ? "    ,    "
              : "     ?"}
          </p>
        </div>
        <div className={styles.buttons}>
          <ButtonDesktop
            view="tertiary"
            onClick={() => {
              //    "  "  ,   ,  
              template && formReady ? handleDeleteTask() : template? handleDeleteTask() : handleCancelTask();
            }}
          >
            {template && formReady
              ? "  "
              : template
              ? ""
              : "  "}
          </ButtonDesktop>
          <ButtonDesktop
            view="accent"
            onClick={() => {
              //    ""  ,   ,  
              template && formReady
                ? handleCreateTask()
                : template
                ? setOpen(false)
                : handleSaveTask();
            }}
          >
            {template && formReady
              ? ""
              : template
              ? ""
              : ""}
          </ButtonDesktop>
        </div>
      </div>
    </Modal>
  );
};

export default ConfirmModal;
